export default class Singer {   //创建个singer的类
  constructor({id,name}){
    this.id = id
    this.name  = name
    this.avatar = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${id}.jpg?max_age=2592000`

//计算过来的avator
}
}



